import React from 'react';
import './App.css';
import PropTypes from 'prop-types';
import ChatBot, { Loading } from 'react-simple-chatbot';
import axios from 'axios'
import https from 'https'
import FeedbackButton from './FeedbackButton'

const URL = "https://greetez.com:4240/predict"
const feedbackURL = "https://greetez.com:4240/feedback"
const myURL = "https://pal-ai.github.io/chat"
process.env.NODE_TLS_REJECT_UNAUTHORIZED = '0';

const uuidv4 = require('uuid/v4');
const myuuid = uuidv4();

var history = [];
var start = ['What are you doing today?', 'What are you doing right now?', 'What\'s up?', 'How are you today?', 'What do you like to do?', 'How are you?']

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

function getRandomStart(){
  return(start[getRandomInt(start.length)])
}


class Thanks extends React.Component {
  constructor(props) {
    super(props);
    this.state = {loading: true, result: <Loading />, trigger: false};
    this.state.result = '';
    this.searchInputTimeout = setTimeout(() => {this.sendfeedback(props).then(
      myresponse => {
        console.log(myresponse);
        this.refresh();
      });
  }, 400);
  }
  render() {
    return (this.state.result);
  }

  sendfeedback(props) {
    var tosend = {
      "history" : history,
      "id" : myuuid,
      'feedback' :props.steps['user-feedback']['message']
    }
    const instance = axios.create({
      httpsAgent: new https.Agent({  
        rejectUnauthorized: false,
        strictSSL: false
      })
    });

    return instance.post(feedbackURL,tosend)
    .then(function(response) {
      console.log(response.data);
      return response.data;
    })
    .catch(err=>console.log(err))
  }
  refresh() {
    window.location.replace(myURL);
  }
}



class Interfacer extends React.Component{
  constructor(props) {
    super(props);
    this.state = {loading: true, result: <Loading />, trigger: false};
    history.push(props.previousStep.message);
    this.searchInputTimeout = setTimeout(() => {this.makeprediction().then(
      myresponse => {
      this.state.result = myresponse;
      history.push(myresponse);
      this.props.triggerNextStep();
    });
  }, 400);
  }

  addquerytohistory(text) {
    var textarr = [text];
    this.setState((state) => {
      return {history: state.history.concat(textarr)} 
    });
  }

  makeprediction(){
    var tosend = {
      "history" : history,
      "device" : "cpu",
      "top_p" : 0.0,
      "top_k" : 10,
      "temperature" : 1,
      "id" : myuuid
    }
    const instance = axios.create({
      httpsAgent: new https.Agent({  
        rejectUnauthorized: false,
        strictSSL: false
      })
    });

    return instance.post(URL,tosend)
    .then(function(response) {
      return response.data;
    })
    .catch(err=>console.log(err))
  }

  triggetNext() {
    this.setState({ trigger: true }, () => {
      this.props.triggerNextStep();
    });
  }
  render() {
    return(this.state.result);
  }
  
}

Interfacer.propTypes = {
  previousStep: PropTypes.object,
  triggerNextStep: PropTypes.func,
};

Interfacer.defaultProps = {
  steps: undefined,
  triggerNextStep: undefined,
};

var steps = [
  {
    id: 'start0',
    message: 'Hello there!',
    trigger: 'start1',
  },
  {
    id: 'start1',
    message: getRandomStart(),
    trigger: 'user',
  },
  {
    id: 'user',
    user: true,
    trigger: 'message-returner',
  },
  {
    id: 'message-returner',
    component: <Interfacer />,
    trigger: 'feedback',
    waitAction: true,
    asMessage: true
  },
  {
    id: 'feedback',
    component: <FeedbackButton />,
    asMessage: true,
    trigger: 'user'
  },
  {
    id: 'feedback-form',
    message: 'What should have I said?',
    trigger: 'user-feedback'
  },
  {
    id: 'user-feedback',
    user: true,
    trigger: 'thanks-message'
  },
  {
    id: 'thanks-message',
    delay: 1500,
    message: 'Thanks!',
    trigger: 'thanks'
  },
  {
    id: 'thanks',
    component: <Thanks feedback/>,
    asMessage: true,
    delay: 100
  }
];

function App() {
  return (
    <div className="App">
      <div>
      <ChatBot width="100%" steps={steps} headerTitle="PAL" contentStyle={{ height: '86vh' }} style={{ height: '100%' }} botDelay={0} userDelay={50} />
      </div>
    </div>
  );
}

export default App;
